import { InnerBlocks } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"

registerBlockType("voblocktheme/columns", {
  title: "Venture Outdoors Columns",
  edit: EditComponent,
  save: SaveComponent
})

function EditComponent(props) {
  return (
    <>
      <div class="col-sm-6 col-lg-4" style={{ backgroundColor: "#333", padding: "35px" }}>
        <p style={{ textAlign: "center", fontSize: "20px", color: "#FFF" }}>Columns</p>
        <InnerBlocks allowedBlocks={["voblocktheme/listitem"]} />
      </div>
    </>
  );
}

function SaveComponent(props) {
  return <InnerBlocks.Content />
}